ar(--clr-cards);\n}\n\n.action-button:hover {\n    background-color: var(--clr-text);\n    color: var(--clr-action-btn);\n}\n/**************SIDEBAR***************/\naside {\n    z-index: 6;\n    height: 100dvh;\n    background-color: var(--clr-aside);\n    align-self: start;\n    display: grid;\n    grid-template-rows: var(--header-size) 1fr;\n    padding-left: 1.2em;\n    box-shadow: 0px 0px 16px 16px rgba(0,0,0,0.4);\n    gap: 1em;\n}\n\naside h1 {\n    font-size: 2.5rem;\n    display: flex;\n    align-items: center;\n}\n\n.sidebar-buttons-container {\n    display: flex;\n    flex-direction: column;\n}\n\n.sidebar-button {\n    /* width: fit-content; */\n    height: 3em;\n    font-size: 1.2em;\n    transition: all 0.05s ease-in-out;\n}\n\n.sidebar-button:hover, .sidebar-button.active {\n    transform: translate(10%, -5%) scale(1.1);\n    cursor: pointer;\n}\n\n.sidebar-button:hover {\n    filter: drop-shadow(0 0 10px white);\n}\n\n/**************CONTENT:CARDS***************/\n\n#content { \n    --gap: 6rem;\n    --shadow: 0px 0px 32px 16px rgba(0,0,0,0.4);\n }\n\n.card.tasks::before { background-color: var(--clr-text); }\n.card.missions::before { background-color: var(--clr-success); }\n.card.offenses::before { background-color: var(--clr-failure); }\n\n.card.tasks:hover { border: 2px solid var(--clr-text); }\n.card.missions:hover { border: 2px solid var(--clr-success); }\n.card.offenses:hover { border: 2px solid var(--clr-failure); }\n\n/* Cards */\n#content:has(.card) {\n    z-index: 1;\n    display: grid;\n    padding: var(--gap);\n    gap: var(--gap);\n    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));\n    grid-auto-rows: max-content;\n    font-size: 90%;\n}\n\n.card {\n    --border-radius: 16px;\n    position: relative;\n    background-color: var(--clr-cards);\n    display: flex;\n    flex-direction: column;\n    justify-content: space-between;\n    padding: 2em;\n    gap: 1em;\n    border-radius: var(--border-radius);\n    border: 2px solid transparent;\n    box-shadow: var(--shadow);\n    transition: all 0.2s ease-in-out;\n}\n\n.card:hover {\n    box-shadow: 0px 0px 32px 28px rgba(0,0,0,0.4);\n    &::before {\n        left: 0;\n    }\n}\n\n.card::before {\n    position: absolute;\n    content: '';\n    z-index: -1;\n    top: 0%;\n    left: -1.5%;\n    height: 100%;\n    width: 100%;\n    border-radius: var(--border-radius);\n    transition: all 0.2s ease-in-out;\n}\n\n.card-header {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n\n    & p { font-weight: 900;}\n}\n\n.card > p {\n    font-size: 1.1rem;\n}\n\n.card-buttons-container {\n    display: flex;\n    gap: 1em;\n    justify-content: center;\n    margin-top: 2em;\n    width: 80%;\n    align-self: center;\n}\n\n.card-button {\n    padding: 0.7em 0px;\n    width: 50%;\n    text-align: center;\n    border-radius: 16px;\n    font-weight: 900;\n    font-size: 110%;\n    color: var(--clr-card-text);\n}\n\n.complete-mission-button, .complete-offense-button {\n    width: 50%;\n    align-self: center;\n}\n\n.success {\n    background-color: var(--clr-success);\n}\n\n.success:hover {\n    background-color: var(--clr-success-hover);\n}\n\n.failure {\n    background-color: var(--clr-failure);\n}\n\n.failure:hover {\n    background-color: var(--clr-failure-hover);\n}\n\n.corner-buttons-container {\n    display: flex;\n    justify-content: flex-end;\n    gap: 1rem;\n    width: 100%;\n}\n\n.corner-button {\n    height: 28px;\n    cursor: pointer;\n    filter: invert(83%) sepia(7%) saturate(1021%) hue-rotate(187deg) brightness(84%) contrast(91%);\n    transition: all 0.15s ease-in-out;\n\n}\n\n.corner-button:hover {\n    transform: scale(1.2);\n    filter: invert(83%) sepia(7%) saturate(1021%) hue-rotate(187deg) brightness(84%) contrast(91%) drop-shadow(0 0 10px white);\n}\n/*******CONTENT:STATS************/\n#content:has(.stats-embed) {\n    --gap-local: calc(var(--gap) / 2);\n    padding: var(--gap-local);\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    min-width: 600px;\n}\n\n#statsTitle {\n    position: relative;\n    width: 100%;\n    margin-bottom: calc(var(--gap-local) / 2);\n}\n\n.stats-embed {\n    background-color: var(--clr-cards);\n    min-height: 60%;\n    width: max(500px, 60%);\n    /* width: 500px; */\n    border-radius: 16px;\n    padding: var(--gap-local);\n    box-shadow: var(--shadow);\n}\n\n#stats {\n    height: 100%;\n    width: 100%;\n    box-shadow: inset 0 0 10px 10px rgba(0,0,0,0.2);\n    background-color: var(--clr-content);\n    border-radius: 16px;\n    padding: 2em;\n}\n\ntd {\n    padding: 1em;\n    font-weight: 900;\n    font-size: 1.2em;\n}\n\ntd:first-child::before {\n    content: \"\\0025CB\\000020\";\n}\n\n\n/* td:nth-child(2) {\n    border-radius: 50%;\n    background-color: white;\n    text-align: center;\n} */\n\n\n/************FORM****************/\ndialog {\n    --gap: 1.5rem;\n\n    place-self: center;\n    background-color: var(--clr-cards);\n    border-radius: 16px;\n    padding: var(--gap);\n\n    border: none;\n    transform: scale(0);\n    visibility: hidden;\n    transition: transform 0.3s ease-in-out, visibility 0s 0.3s;\n\n    & > h1 { /* Modal header */\n        display: grid;\n        place-self: center;\n        margin-bottom: var(--gap);\n    }\n\n    &.show {\n        transform: scale(1);\n        visibility: visible;\n        transition: transform 0.3s ease-in-out, visibility 0s 0s;\n    }\n}\n\nform {\n    display: flex;\n    flex-direction: column;\n    gap: var(--gap);\n    font-size: 1em;\n}\n\n.name, .desc {\n    display: flex;\n    flex-direction: column;\n}\n\ninput {\n    outline: none;\n    border: none;\n    border-radius: 16px;\n    background-color: var(--clr-content);\n    padding: 0.3em 1em;\n    font-size: 1em;\n}\n\n.points-general {\n    display: flex;\n    flex-direction: row;\n    gap: 1em;\n\n    & * { /* Reward and Penalty divs */\n        flex: 1;\n        display: flex;\n        flex-direction: column;\n    }\n}\n\n.form-buttons {\n    display: flex;\n    gap: 1em;\n\n    & button {\n        flex: 1;\n        border-radius: 16px;\n        background-color: var(--clr-content);\n        padding: 0.3em 0;\n\n        &:hover {\n            background-color: var(--clr-text);\n            color: var(--clr-content);\n        }\n    }\n}\n\ninput:valid {\n    border: 1.5px solid var(--clr-card-text);\n}\n\ninput:invalid {\n    border: 1.5px solid red;\n}\n\n::backdrop {\n    background-color: rgba(0,0,0,0.4);\n}\n/************FEEDBACK STRIP****************/\n\n.feedback-container {\n    z-index: 900;\n    position: fixed;\n    right: 0;\n    bottom: 10%;\n    min-width: 300px;\n    color: white;\n    text-align: center;\n    font-weight: 900;\n    box-shadow: 0 0 16px 16px rgba(0,0,0,0.4);\n    transform: translateX(150%);\n}\n\n.feedback {\n    background-color: green;\n    transition: all 0.3s ease-in-out;\n    height: 100%;\n    width: 100%;\n    font-size: 1rem;\n    padding: 0.75em 1.25em;\n}\n\n.feedback.visible {\n    transform: translateX(-150%);\n}\n\n.feedback.fail {\n    background-color: red;\n}\n/********************************/\n"],"sourceRoot":""}]);
